.top__section {
	position: relative;
	z-index: 1;
	padding-bottom: 80px;
	margin-bottom: 80px;
	.top__section-in {
		position: relative;
		z-index: 4;
		padding: 80px;
		.top__header {
			margin: 0 -15px;
			.top__header-logo {
				max-width: 100%;
				height: auto; }
			.top__header-sup {
				line-height: 55px;
				color: white;
				font-weight: 300; }
			.top__header-sub {
				line-height: 38px;
				color: white;
				font-weight: 300; } }
		.top__header-button {
			margin: 100px -15px; }
		.top__section-over {
			position: relative;
			height: 100%;
			z-index: -1;
			.top__section-over-in {
				position: absolute;
				z-index: 2;
				transform: translate(0,-50%);
				top: 78px;
				left: 0;
				@include r(768) {
					position: relative;
					transform: translate(0, 0); }
				.top__dev {
					position: relative;
					width: auto;
					@include r(768) {
						max-width: 100%; } }
				.top__dev1 {
					top: 190px;
					left: 30px;
					@include r(768) {
						top: 0;
						left: 0; } }
				.top__dev2 {
					top: 340px;
					left: -40px;
					z-index: 5;
					@include r(768) {
						top: 0;
						left: 0; } }
				.top__dev3 {
					z-index: 4;
					left: -190px;
					top: -22px;
					@include r(768) {
						top: 0;
						left: 0; } } } } }

	.top__section-bg {
		@include coverdiv;
		z-index: 2;
		background: url('./../img/top-bg.png') center/cover no-repeat fixed #FFF; }
	.top__section-grad {
		@include coverdiv;
		z-index: 3;
		background-image: linear-gradient(to top, transparent 0%, rgba(0, 0, 0, 0.21) 100%); } }

