.pages {
	position: relative;
	z-index: 1;
	color: $brooks-text;
	padding-top: 300px;
	text-align: center;
	&:after {
		content: " ";
		position: absolute;
		z-index: 3;
		bottom: 0;
		height: 100px;
		left: 0;
		right: 0;
		background-image: linear-gradient(180deg, transparent 0%,  white 70%, white 100%); }
	.pages__header {
		width: 100%;
		height: 100%;
		@include fl;
		font-weight: 300; }
	.pages__pic-holder {
		position: relative;
		z-index: 3;
		overflow: hidden;
		&:hover {
			.pages__pic-hover {
				visibility: visible;
				opacity: 1; } }
		.pages__pic {
			object-fit: cover;
			-webkit-object-fit: cover;
			width: 100%;
			height: auto; }
		.pages__pic-hover {
			@include coverdiv;
			left: 9px;
			width: 95%;
			height: 91%;

			z-index: 4;
			background: rgba(#000 , .7);
			visibility: hidden;
			opacity: 0;
			@include trans(all,.5s);


			.pages__ref {
				@include vcenter;
				color: #fff;
				@include trans(all, .5s, cubic-bezier(0.5, -0.5, 0.5, 1.5) );
				padding-left: 35px;
				&:hover {
					color: $brooks-magenta; }
				&:hover:after {
					@include trans(all, .5s, cubic-bezier(0.5, -0.5, 0.5, 1.5));
					left: 20px;
					color: $brooks-magenta; }
				&:after {
					@include trans(all, .5s, cubic-bezier(0.5, -0.5, 0.5, 1.5));
					position: relative;
					top: -2px;
					left: 10px;
					content: "→"; } } } }
	.pages__desc {
		text-align: center;
		padding-bottom: 35px; } }

