.responsive {
	position: relative;
	z-index: 2;
	padding-top: 70px;
	.responsive__header {
		@include fl; }
	.responsive__dev-block {
		position: relative;
		z-index: 1;
		.responsive__dev-block-in {
			position: relative;
			bottom: -40px;
			width: 100%;
			display: flex;
			.responsive__dev-out {
				display: flex;
				.responsive__dev {
					margin-top: auto; } } } }
	.responsive__bg-out {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		z-index: -1;
		.responsive__bg {
			position: absolute;
			top: 0;
			z-index: 2;
			width: 100%;
			height: 100%; }
		.responsive__bg1 {
			background:  url('./../img/responsive_layer1.png') 0 100%/auto auto  repeat-x transparent; }
		.responsive__bg2 {
			z-index: 3;
			background:  url('./../img/responsive_layer2.png') 0 100%/auto auto  repeat-x transparent; }
		.responsive__bg3 {
			z-index: 4;
			background:  url('./../img/responsive_layer3.png') 0 100%/auto auto  repeat-x transparent; }
		.responsive__bg4 {
			z-index: 5;
